/**
 * Calcula el pago mensual de un prstamo basado en el monto, tasa de inters y plazo
 * @param loanAmount - Monto del prstamo
 * @param interestRate - Tasa de inters anual (en decimales, ej: 0.12 para 12%)
 * @param termMonths - Plazo en meses
 * @returns Pago mensual calculado
 */
var calculateMonthlyPayment = function (loanAmount, interestRate, termMonths) {
    // Convertir tasa de inters anual a mensual
    var monthlyInterestRate = interestRate / 12;
    // Si la tasa es 0, simplemente dividir el monto entre los meses
    if (interestRate === 0) {
        return loanAmount / termMonths;
    }
    // Frmula para calcular pago mensual: P = L[r(1+r)^n]/[(1+r)^n-1]
    // Donde: P = pago mensual, L = monto del prstamo, r = tasa mensual, n = nmero de pagos
    var numerator = monthlyInterestRate * Math.pow(1 + monthlyInterestRate, termMonths);
    var denominator = Math.pow(1 + monthlyInterestRate, termMonths) - 1;
    var monthlyPayment = loanAmount * (numerator / denominator);
    // Redondear a 2 decimales y devolver
    return Math.round(monthlyPayment * 100) / 100;
};
exports.calculateMonthlyPayment = calculateMonthlyPayment;
