// Funciones de utilidad para interactuar con Supabase
export const fetchApplications = async (filters = {}) => {
  try {
    let query = supabase.from('applications').select('*');
    
    // Aplicar filtros si existen
    if (filters.status) {
      query = query.eq('status', filters.status);
    }
    
    if (filters.searchQuery) {
      query = query.or(`client_name.ilike.%${filters.searchQuery}%,client_email.ilike.%${filters.searchQuery}%`);
    }
    
    if (filters.application_type) {
      query = query.eq('application_type', filters.application_type);
    }
    
    if (filters.dateFrom) {
      query = query.gte('created_at', filters.dateFrom);
    }
    
    if (filters.dateTo) {
      query = query.lte('created_at', filters.dateTo);
    }
    
    if (filters.amountMin) {
      query = query.gte('amount', filters.amountMin);
    }
    
    if (filters.amountMax) {
      query = query.lte('amount', filters.amountMax);
    }
    
    const { data, error } = await query;
    
    if (error) {
      throw new Error(`Error fetching applications: ${error.message}`);
    }
    
    return data;
  } catch (error) {
    console.error("Error in fetchApplications:", error);
    throw error;
  }
};

export const updateApplicationStatus = async (applicationId, newStatus, userId, comment = '') => {
  try {
    // Actualizar el estado de la aplicacin
    const { data, error } = await supabase
      .from('applications')
      .update({ 
        status: newStatus,
        updated_at: new Date().toISOString()
      })
      .eq('id', applicationId)
      .select();
    
    if (error) {
      throw new Error(`Error updating application status: ${error.message}`);
    }
    
    // Registrar el cambio en el historial
    const { error: historyError } = await supabase
      .from('application_history')
      .insert({
        application_id: applicationId,
        status: newStatus,
        user_id: userId,
        comment: comment,
        created_at: new Date().toISOString()
      });
    
    if (historyError) {
      console.error("Error recording status change in history:", historyError);
    }
    
    return data[0];
  } catch (error) {
    console.error("Error in updateApplicationStatus:", error);
    throw error;
  }
};

export const getApplicationHistory = async (applicationId) => {
  try {
    const { data, error } = await supabase
      .from('application_history')
      .select('*')
      .eq('application_id', applicationId)
      .order('created_at', { ascending: false });
    
    if (error) {
      throw new Error(`Error fetching application history: ${error.message}`);
    }
    
    return data;
  } catch (error) {
    console.error("Error in getApplicationHistory:", error);
    throw error;
  }
};

export const approveApplication = async (applicationId, isAdvisor, userId, comment = '') => {
  try {
    const updateData = {};
    
    if (isAdvisor) {
      updateData.approved_by_advisor = true;
      updateData.approval_date_advisor = new Date().toISOString();
    } else {
      updateData.approved_by_company = true;
      updateData.approval_date_company = new Date().toISOString();
    }
    
    const { data, error } = await supabase
      .from('applications')
      .update(updateData)
      .eq('id', applicationId)
      .select();
    
    if (error) {
      throw new Error(`Error approving application: ${error.message}`);
    }
    
    // Registrar la aprobacin en el historial
    const approvalType = isAdvisor ? 'advisor' : 'company';
    const historyComment = comment || `Application approved by ${approvalType}`;
    
    await supabase
      .from('application_history')
      .insert({
        application_id: applicationId,
        status: `approved_by_${approvalType}`,
        user_id: userId,
        comment: historyComment,
        created_at: new Date().toISOString()
      });
    
    return data[0];
  } catch (error) {
    console.error(`Error in approveApplication (${isAdvisor ? 'advisor' : 'company'}):`, error);
    throw error;
  }
};

export const getApprovalStatus = async (applicationId) => {
  // Store last successful result for this application ID
  const cachedResultKey = `approval_status_${applicationId}`;
  
  try {
    // First try to get from the session cache if available
    try {
      const cachedData = sessionStorage.getItem(cachedResultKey);
      if (cachedData) {
        console.log(`Using cached approval status for ${applicationId}`);
        const parsedData = JSON.parse(cachedData);
        // Only use cache if it contains both flags
        if ('approved_by_advisor' in parsedData && 'approved_by_company' in parsedData) {
          return {
            approvedByAdvisor: parsedData.approved_by_advisor || false,
            approvedByCompany: parsedData.approved_by_company || false,
            approvalDateAdvisor: parsedData.approval_date_advisor,
            approvalDateCompany: parsedData.approval_date_company
          };
        }
      }
    } catch (e) {
      // Ignore storage errors
    }
    
    // If not in cache, fetch from Supabase
    const { data, error } = await supabase
      .from('applications')
      .select('approved_by_advisor, approved_by_company, approval_date_advisor, approval_date_company')
      .eq('id', applicationId)
      .single();
    
    if (error) {
      throw new Error(`Error fetching approval status: ${error.message}`);
    }
    
    // Cache successful result in session storage
    try {
      sessionStorage.setItem(cachedResultKey, JSON.stringify(data));
    } catch (e) {
      // Ignore storage errors
    }
    
    return {
      approvedByAdvisor: data.approved_by_advisor || false,
      approvedByCompany: data.approved_by_company || false,
      approvalDateAdvisor: data.approval_date_advisor,
      approvalDateCompany: data.approval_date_company
    };
  } catch (error) {
    console.error("Error in getApprovalStatus:", error);
    
    // Try to return cached data if available
    try {
      const cachedData = sessionStorage.getItem(cachedResultKey);
      if (cachedData) {
        console.log(`Using cached approval status for ${applicationId} after error`);
        const parsedData = JSON.parse(cachedData);
        return {
          approvedByAdvisor: parsedData.approved_by_advisor || false,
          approvedByCompany: parsedData.approved_by_company || false,
          approvalDateAdvisor: parsedData.approval_date_advisor,
          approvalDateCompany: parsedData.approval_date_company
        };
      }
    } catch (e) {
      // Ignore storage errors
    }
    
    // Return default values instead of throwing to prevent UI errors
    return {
      approvedByAdvisor: false,
      approvedByCompany: false,
      approvalDateAdvisor: null,
      approvalDateCompany: null
    };
  }
};

